a {
  color: #E95258;
  transition: color 0.2s;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

a a.white-hover:hover {
  color: #F9F9F9;
}

.link-hover-accent a, a.link-hover-accent {
  color: inherit;
}

.link-hover-accent a:hover, a.link-hover-accent:hover {
  color: #E95258;
}

ul {
  padding-left: 1.3em;
}

.ul-circle, .ul-circle-right {
  list-style: none;
  padding: 0;
  margin-bottom: 1.5em;
}

.ul-circle li, .ul-circle-right li {
  position: relative;
  margin-bottom: 0.85em;
}

.ul-circle li:before, .ul-circle-right li:before {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 50%;
  border: 2px solid;
  top: 5px;
  vertical-align: baseline;
}

.ul-circle {
  padding-left: 20px;
}

.ul-circle li:before {
  left: -20px;
}

.ul-circle-right {
  padding-right: 20px;
}

.ul-circle-right li::before {
  right: -20px;
}

.btn {
  color: #32495E;
  font-size: 0.875rem;
  padding: 0.57143em 1.78571em;
  border-radius: 1.78571em;
  min-width: 13.57143em;
  text-decoration: none;
  flex-shrink: 1;
  background-color: transparent;
  position: relative;
  overflow: hidden;
  transition: color 0.4s ease-in-out, box-shadow 0.4s ease-in-out;
  box-shadow: inset 0 0 0 1px #32495E;
}

.btn:hover, .btn:focus, .btn:active {
  text-decoration: none;
  color: #F9F9F9;
  box-shadow: inset 0 0 0 1px #E95258, inset 0 0 2em 3em #E95258;
}

button.btn {
  min-height: 3.57143em;
}

a.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

a.btn::before {
  content: '';
  width: 0;
  display: block;
  height: 2.42857em;
}

.btn-hover-black:hover, .btn-hover-black:focus, .btn-hover-black:active {
  color: #F9F9F9;
  box-shadow: inset 0 0 3em 3em #32495E;
  border-color: #32495E;
}

.btn--white {
  color: #F9F9F9;
  border-color: #F9F9F9;
  box-shadow: inset 0 0 0 1px #F9F9F9;
}

.btn--white-hover-black:hover, .btn--white-hover-black:focus, .btn--white-hover-black:active {
  color: #F9F9F9;
  box-shadow: inset 0 0 0 1px #32495E, inset 0 0 3em 3em #32495E;
  border-color: #32495E;
}

.btn--accent {
  color: #E95258;
  border-color: #E95258;
}

.btn--center {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
}

.btn--to-up {
  background: #E95258;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  position: relative;
  margin-bottom: 5.35714em;
  transition: box-shadow 0.2s;
}

.btn--to-up svg {
  vertical-align: top;
  animation: btnToUp 1s ease-in-out 0s infinite alternate;
}

.btn--to-up path {
  fill: #F9F9F9;
}

.btn--to-up::after {
  content: 'На вверх';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-54%, 100%) rotate(-90deg);
  white-space: nowrap;
  line-height: 1;
  margin-bottom: -2.14286em;
  pointer-events: none;
}

.btn--to-up:hover, .btn--to-up:focus {
  box-shadow: 0px 0px 10px rgba(233, 82, 88, 0.7);
}

.btn--next-scroll {
  width: 60px;
  height: 60px;
  background: #F2C94C;
  border-radius: 50%;
  transition: box-shadow 0.2s;
  z-index: 10;
}

.btn--next-scroll img {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.btn--next-scroll .mouse {
  margin-bottom: 5px;
  animation: btnToUp 1s ease-in-out 0s infinite alternate;
}

.btn--next-scroll:hover {
  box-shadow: 0px 0px 10px rgba(242, 201, 76, 0.7);
}

@keyframes btnToUp {
  0% {
    transform: translate(0, 1px);
  }
  100% {
    transform: translate(0, -1px);
  }
}

.btn-more, .btn-what, .btn-write, .btn-phone, .btn-inst {
  width: 50px;
  height: 50px;
  border: 1px solid #fff;
  border-radius: 50%;
  transition: box-shadow 0.4s ease-in-out;
}

a.btn-more, a.btn-what, a.btn-write, a.btn-phone, a.btn-inst {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

