  box-shadow: 0 0 0 0 rgba(20, 172, 81, 0.1);
}

.btn-what:hover {
  box-shadow: 0 0 0 5px rgba(20, 172, 81, 0.3);
}

.btn-write {
  background: #4598FF;
  box-shadow: 0 0 0 0 rgba(69, 152, 255, 0.1);
}

.btn-write:hover {
  box-shadow: 0 0 0 5px rgba(69, 152, 255, 0.3);
}

.btn-phone {
  background: #32495E;
  box-shadow: 0 0 0 0 rgba(50, 73, 94, 0.1);
}

.btn-phone:hover {
  box-shadow: 0 0 0 5px rgba(50, 73, 94, 0.3);
}

.btn-inst {
  background: #C33A7C;
  box-shadow: 0 0 0 0 rgba(195, 58, 124, 0.1);
}

.btn-inst:hover {
  box-shadow: 0 0 0 5px rgba(195, 58, 124, 0.3);
}

@keyframes btn-more {
  0% {
    box-shadow: 0 0 0 0 rgba(233, 82, 88, 0.5);
  }
  15% {
  }
