/* LOGO */
.logo {
  color: #32495E;
  display: flex;
  align-items: center;
  font-size: 1.25rem;
}

@media (min-width: 48em) {
  .logo {
    font-size: 1.5rem;
  }
}

@media (min-width: 75em) {
  .logo {
    font-size: 1.75rem;
  }
}

@media (min-width: 88em) {
  .logo {
    font-size: 1.875rem;
  }
}

.logo__svg {
  width: 2.33333em;
  height: 1.5em;
  flex: 0 0 2.33333em;
}

.logo__svg path:nth-child(1) {
  fill: #32495E;
