$size-margin: 14*$base-size; 
.section-chose {
  display: flex;
  &::before {
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      content: '';
      height: calc(100vh - 60px);
      width: 0;
      display: block;
      max-height: 770px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl'))  {
      max-height: 900px;
    }
  }
  &__outer {
    overflow: hidden;
    display: flex;
    align-items: flex-end;
    width: 100%;
  }
  &__container {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    flex-wrap: wrap;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      flex-wrap: nowrap;
      transform: translate(-#{$size-margin}, 0);
      transition: transform $transition ease-in-out;
      height: 100%;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      height: auto;
    }
    // @include mediaForPhone {
    //   flex-wrap: wrap;
    //   transform: translate(0, 0);
    // }
  }
  &__right, &__left {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-shrink: 0;
    width: 100%;
    padding-top: 30px;
    margin: 0 auto;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      max-width: map-get($container-max-widths, 'sm');
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      width: 50%;
      padding-top: 40px;
      max-width: map-get($container-max-widths, 'md');
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: calc(50% + #{$size-margin});
      max-width: none;
    }
    // @include mediaForPhone {
    //   width: 100%;
    // }
  }
  &__left {
    background: $color-accent;
    color: $color-white;
    align-items: flex-start;
    padding-left: 15px;
    padding-right: 15px;
    position: relative;
    .ul-circle-right {
      padding-left: 20px;
      padding-right: 0;
      li {
        &:before {
          left: -20px;
          right: auto;
        }
      }
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      padding-left: 15px;
      padding-right: 60px;
      text-align: right;
      align-items: flex-end;
      .ul-circle-right {
        padding-left: 0px;
        padding-right: 20px;
        li {
          &:before {
            right: -20px;
            left: auto;
          }
        }
      }
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      padding-left: 0;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      padding-right: 80px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      padding-right: 100px;
    }
    & > * {
      z-index: 1;
    }
    @include mediaForPhone {
      // text-align: left;
      // padding-left: 60px;
      // align-items: flex-start;
    }
    &::after {
      content: '';
      position: absolute;
      background: $color-accent;
      width: 110vw;
      height: 100%;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0);
      @media (min-width: map-get($grid-breakpoints, 'md'))  {
        width: 100%;
        transform: translate(0, 0);
        left: 0;
      }
      @media (min-width: map-get($grid-breakpoints, 'md')) {
        height: 2000px;
      }
      // @include mediaForPhone {
      //   display: none;
      // }
    }
    p, ul {
      @media (min-width: map-get($grid-breakpoints, 'md')) {
        margin-left: auto;
      }
      // @include mediaForPhone {
      //   margin-left: 0;
      // }
    }
  }
  &__right {
    align-items: flex-start;
    padding-left: 15px;
    padding-right: 15px;
    // @include mediaForPhone {
    //   padding-right: 60px;
    // }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      padding-left: 60px;
      padding-right: 15px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      padding-right: 0;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      padding-left: 80px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      padding-left: 100px;
    }
  }
  &__left &__img {
    order: -1;
    // justify-content: flex-start;
    align-items: flex-start;
    max-width: 495px;
    max-height: 567px;
  }
  &__right &__img {
    order: 1;
    // justify-content: flex-end;
    align-items: flex-end;
    max-width: 525px;
    max-height: 528px;

  }
  &__left &__wrapper {
    margin-left: auto;
    align-items: flex-end;
  }
  &__right &__wrapper {
    align-items: flex-start;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      width: 100%;
      flex-grow: 1;
    }
  }
  &__wrapper {
    padding-bottom: 30px;
    @media (min-width: map-get($grid-breakpoints, 'md'))  {
      display: flex;
      flex-direction: column;
      padding-bottom: 40px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
     
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      padding-bottom: 80px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      padding-bottom: 100px;
    }
    // @include mediaForPhone {
    //   min-width: 400px;
    // }
  }
  &__img {
    display: none;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-top: auto;
      align-self: flex-end;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      flex: 1 1 60%
    }
    img {
      // width: 100%;
      flex: 0 0 auto;
    }
  }
  .h2 {
    max-width: 17em;
    margin-bottom: 1.78em;
  }
  .text-big {
    margin-bottom: 1.7em;
    max-width: (400em/16);
    // @media (min-width: map-get($grid-breakpoints, 'lg')) {
    //   max-width: (350em/16);
    // }
   
    // @include mediaForPhone {
    //   max-width: (400em/16);
    // }
  }
  .ul-circle, .ul-circle-right {
    margin-bottom: 2.5em;
    max-width: (400em/14);
    // @media (min-width: map-get($grid-breakpoints, 'lg')) {
    //   max-width: (350em/14);
    // }

    // @include mediaForPhone {
    //   max-width: (400em/14);
    // }
  }

  .btn {
    margin-top: auto;
  }

  .btn--next-scroll {
    display: none;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      display: block;
    }
  }
}

.section-chose.hover-left {
  .section-chose__container {
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      transform: translate(0, 0);
    }
  }
}

.section-chose.hover-right {
  .section-chose__container {
    @media (min-width: map-get($grid-breakpoints, 'lg'))  {
      transform: translate(-#{$size-margin*2}, 0);
    }
    // @include mediaForPhone  {
    //   transform: translate(0, 0);
    // }
  }
}