.grid-feature {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: -15px -20px;
    // justify-content: center;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      justify-content: flex-start;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      margin: -15px -20px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      margin: -15px -25px;
    }
  }
  &__item {
    width: 100%;
    padding: 15px 20px;
    // text-align: center;
    // max-width: 400px;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      text-align: left;
      width: 50%;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: 25%;
      text-align: center;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      padding: 15px 20px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      padding: 15px 25px;
    }
  }
  &__img {
    border: 2px solid $color-accent;
    width: 78px;
    border-radius: 50%;
    max-width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    padding: 10px;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      width: 98px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-bottom: 30px;
    }
    &::before {
      content: '';
      width: 0;
      padding-top: 100%;
      display: block;
    }
    img, svg {
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
    }
  }
  &__title {
    margin-bottom: 1em;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-bottom: (20em/16);
    }
  }
}