.input:not([type="radio"]):not([type="checkbox"]) {
  font: inherit;
  color: $color-input;
  background: none;
  max-width: 100%;
  border: 0;
  border-bottom: 2px solid $color-grey;
  font-size: (14rem/16);
  font-weight: 500;
  display: block;
  width: 100%;
  // Нельзя ставить свойство line-height
  padding: 0px (8em/14);
  height: (42em/14);
  margin-bottom: 2em;
  -webkit-appearance: none;
  border-radius: 0;
  @include input-placeholder {
    opacity: 1; // Не через opacity делать прозрачный цвет, потому что в ie это распространяется и на сам input
    color: rgba($color-input, 0.6)
  }
}

.input-input {
  & + & {
    margin-top: -1em;
  }
}