/* SECTION */
.section {
  padding-top: 14*$base-size;
  padding-bottom: 8*$base-size;
  &__title {
    margin-bottom: 0.95em;
  }
}
.section--black {
  background: $color-black;
  color: $color-white;
}
.section--white {
  color: $color-black;
  background: $color-white;
}
.section--eq-padding {
  padding-top: 12*$base-size;
  padding-bottom: 12*$base-size;
}
.section--with-btn {
  position: relative;
  .container > .btn:last-child {
    margin-top: 40px;
  }
}
.section--two-line {
  z-index: 0;
  &__decor {
    z-index: -1;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    &::after {
      content: '';
      position: absolute;
      width: 186px;
      height: 100%;
      background: #49647D;
      background: linear-gradient(90deg, $color-back-line-section 0%, $color-back-line-section 35%, transparent 35%, transparent 65%, $color-back-line-section 65%);
      top: 0;
      right: 15%;
      z-index: -4;
      @media (min-width: map-get($grid-breakpoints, 'xxl')) {
        right: calc(50vw - #{map-get($container-max-widths, 'xxl')/2} + 50px);
      }
    }
  }  
}

.section--line-title {
  .section__title {
    position: relative;
    display: block;
    z-index: 0;
    &::after {
      content: '';
      position: absolute;
      height: 130%;
      width: 110vw;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: $color-back-line-section;
      z-index: -1;
    }
  }
}

.section--next-scroll {
  position: relative;
  .btn--next-scroll {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 55%);
    z-index: 1;
  }
}

.section--sub-first {
  position: relative;
  z-index: 0;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    opacity: 0.7;
    z-index: -1;
  }
  .h1 {
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-top: 2.3em;
    }
    margin-bottom: 0.5em;
  }
}

.section--sub-first-black {
  color: $color-white;
  &::after {
    background: $color-black;
  }
}

.section--nav-line {
  padding-top: 0;
  padding-bottom: 10*$base-size;
  .section--nav-line__nav {
    position: relative;
    z-index: 0;
    display: flex;
    align-items: center;
    margin-bottom: 10*$base-size;
    padding-top: 10px;
    padding-bottom: 10px;
    color: $color-white;
    a {
      color: $color-white;
      &:hover {
        color: $color-accent;
      }
    }
    &::before {
      content: '';
      height: 36px;
      display: block;
      width: 0;
    }
    &::after {
      content: '';
      position: absolute;
      height: 100%;
      width: 110vw;
      top: 0%;
      left: 50%;
      transform: translate(-50%, 0%);
      background: #2B3946;
      z-index: -1;
    }
    .standard-nav {
      margin-bottom: 0;
    }
    .standard-nav__btn {
      margin-top: 2px;
      margin-bottom: 9px;
    }

  }
}

