.fhoto-video-card {
  display: flex;
  flex-wrap: wrap;
  &__item {
    width: 100%;
    position: relative;
    cursor: zoom-in;
    @media (min-width: 26.25em) {
      width: 50%;
    }
    @media (min-width: map-get($grid-breakpoints, 'md'))  {
      width: 33.333%;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg'))  {
      width: 25%;
    }
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba($color-black, 0.6);
      transition: opacity $transition ease-in-out;
    }
  }
  &__btn {
    position: absolute;
    background: rgba($color-black, 0.6);
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -120%);
    min-width: (145em/14);
    opacity: 0;
    transition: color $transition ease-in-out, box-shadow $transition ease-in-out, transform $transition ease-in-out, opacity $transition ease-in-out;
  }
  &__name {
    position: absolute;
    bottom: $base-size*4;
    left: $base-size*4;
    right: $base-size*4;
    z-index: 1;
    transition: opacity $transition ease-in-out, transform $transition ease-in-out;
  }

  &__item:hover::after {
    opacity: 0;
  }
  &__item:hover &__name,  &__item:hover .icon-video{
    opacity: 0;
    transform: translate(0, 80%);
  } 
  &__item:hover &__btn {
    transform: translate(-50%, -50%);
    opacity: 1;
  }

  &__item--video {
    .icon-video {
      display: block;
      width: 24px;
      height: 24px;
      opacity: 1;
      visibility: initial;
      z-index: 1;
      top: auto;
      bottom: calc(2.4em + #{$base-size*7});
      left: $base-size*4;
      transition: opacity $transition ease-in-out, transform $transition ease-in-out;
    }
    // &::before {
    //   content: '';
    //   display: block;
    //   background: url('../img/icon-video.svg') no-repeat center center;
    //   background-size: contain;
    //   width: 24px;
    //   height: 24px;
    //   position: absolute;
    //   bottom: calc(2.3em + #{$base-size*6});
    //   left: $base-size*4;
    //   z-index: 1;
    // }
  }
}
