
/* TYPEOGRAFY */
html, body {
  font-size: 1rem;
  color: $color-black;
  line-height: 1.2;
}
iframe {
  border: 0px;
  background: $color-grey;
}
.container {
  & > * {
    @include size-standart;
  }
}
p, span, ul, ol {
  @include size-standart;
}
.text-big {
  &, p, span, ul, ol {
    @include size-big-text;
  }
}
.substronges {
  font-weight: $font-substrong;
}
.stronges {
  font-weight: $font-strong;
}
h1, h2, h3, h4, h5, h6 {
  margin-bottom: (20rem/16);
}
.h1, .h2 {
  text-transform: uppercase;
}
.h1 {
  font-weight: $font-weight-h1;
  @include size-h1;
}
.h2 {
  font-weight: $font-weight-h2;
  @include size-h2;
}
.h3 {
  font-weight: $font-weight-h3;
  @include size-h3;
}

a {
  color: $color-accent;
  transition: color $transition-q;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
  a.white-hover {
    &:hover {
      color: $color-white;
    }
  }
}

.link-hover-accent a, a.link-hover-accent{
  color: inherit;
  &:hover {
    color: $color-accent;
  }
}

ul {
  padding-left: 1.3em;
}

.ul-circle, .ul-circle-right {
  list-style: none;
  padding: 0;
  margin-bottom: 1.5em;
  li {
    position: relative;
    margin-bottom: 0.85em;
    &:before {
      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      display: inline-block;
      border-radius: 50%;
      border: 2px solid;
      top: 5px;
      vertical-align: baseline;
    }
  }
}

.ul-circle {
  padding-left: 20px;
  li {
    &:before {
      left: -20px;
    }
  }
}

.ul-circle-right {
  padding-right: 20px;
  li {
    &::before {
      right: -20px;
    }
  }
}

@import 'btn';