.grid-contact-btn {
  display: flex;
  position: fixed;
  z-index: 10;
  bottom: 25px;
  right: 25px;
  flex-direction: column;
  align-items: flex-end;
  transition: transform $transition cubic-bezier(.18,.89,.32,1.28);
  @media (min-width: map-get($grid-breakpoints, 'md')) {
    right: 40px;
    flex-direction: row;
    align-items: center;
  }
  button, a {
    flex-shrink: 0;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 30px;
    pointer-events: none;
    position: absolute;
    visibility: hidden;
    right: 1px;
    bottom: 100%;
    transition: visibility 0s $transition;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      bottom: 1px;
      right: 100%;
      margin-right: 30px;
      margin-bottom: 0;
      flex-direction: row;
    }
    button + button, button + a, a + button, a + a {
      margin-top: 20px; 
      @media (min-width: map-get($grid-breakpoints, 'md')) {
        margin-left: 20px;
        margin-top: 0;
      }
    }
    button, a {
      width: 50px;
      height: 50px;
      transition: box-shadow $transition ease-in-out, transform $transition ease-in-out;
      &:nth-child(4) {
        transform: translateY(81px);
      }
      &:nth-child(3) {
        transform: translateY(#{81px + 70px});
      }
      &:nth-child(2) {
        transform: translateY(#{81px + 70px * 2});
      }
      &:nth-child(1) {
        transform: translateY(#{81px + 70px * 3});
      }
      @media (min-width: map-get($grid-breakpoints, 'md')) {
        &:nth-child(4) {
          transform: translateX(81px);
        }
        &:nth-child(3) {
          transform: translateX(#{81px + 70px});
        }
        &:nth-child(2) {
          transform: translateX(#{81px + 70px * 2});
        }
        &:nth-child(1) {
          transform: translateX(#{81px + 70px * 3});
        }
      }
    }
    &.active {
      pointer-events: initial;
      visibility: initial;
      transition: visibility 0s 0s;
      button, a {
        // &:nth-child(4) {
        //   transform: translateX(81px) translateY(-81px);
        // }
        // &:nth-child(3) {
        //   transform: translateX(#{81px + 70px}) translateY(-#{81px + 70px});
        // }
        // &:nth-child(2) {
        //   transform: translateX(#{81px + 70px * 2})  translateY(-#{81px + 70px * 2});
        // }
        // &:nth-child(1) {
        //   transform: translateX(#{81px + 70px * 3})  translateY(-#{81px + 70px * 3});
        // }
        transform: translate(0, 0) !important;
      }
    }
  }
  .btn-more {
    z-index: 10;
    width: 52px;
    height: 52px;
  }
}

.grid-contact-btn.scroll-hide {
  transition: transform $transition cubic-bezier(.6,-0.28,.74,.05);
  transform: translate(0, 120px);
}