.price-service {
  width: 100%;
  &__item {
    td {
      padding-bottom: 20px;
    }
  }
  &__header {
    td {
      padding-bottom: 30px;
    }
  }
  &__right {
    white-space: nowrap;
    width: auto;
  }
  &__left {
    width: 100%;
    padding-right: 1.5em;
  }
}

.price-service--grey-color {
  .price-service__item {
    td:first-child {
      color: $color-grey;
    }
  }
}

.price-service-wrapper-two-col {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  table {
    width: 100%;
    flex: 0 0 100%;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: calc(50% - 35px);
      flex: 0 0 calc(50% - 35px);
    }
  }
  table:last-child {
    .price-service__header {
      display: none;
      @media (min-width: map-get($grid-breakpoints, 'lg')) {
        display: table-row;
      }
    }
  }
}

.price-service--sort table, table.price-service--sort {
  position: relative;
  // display: block;
  transition: height $transition;
  tr {
    width: 100%;
  }
}