header + section:not(.section-chose), header + section.section-chose .section-chose__outer {
  //Первоначальный отступ для шапки
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    padding-top: 64px;
  }
}

.header {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  position: relative;
  @media (min-width: map-get($grid-breakpoints, 'md')) {
    padding-top: 2*$base-size;
    padding-bottom: 2*$base-size;
  }
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    position: absolute;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    height: 45px;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      height: auto;
    }
  }
  &__logo {
    margin-right: auto; 
    align-self: center;
    height: auto;
    max-height: 100%;
  }
  &__list {
    display: flex;
    position: absolute;
    list-style: none;
    padding: 0;
    font-weight: $font-medium;
    // opacity: 0;
    flex-direction: column;
    right: 0;
    top: 45px;
    background: $color-black;
    text-align: right;
    color: $color-white;
    width: 210px;
    padding: 20px 14px;
    transition: transform $transition;
    transform: rotate(20deg) translate(100%, 0);
    transform-origin: 100% 100%;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      margin: (-10em/14) (-20em/14);
      flex-direction: row;
      position: relative;
      opacity: 1;
      background: none;
      text-align: inherit;
      color: inherit;
      align-items: center;
      top: 0px;
      width: auto;
      transform: translate(0, 0) rotate(0deg);
    }
  }
  &__item {
    margin: (10em/14) ((20em/14) - 0.5em);
    & > a, & > span {
      white-space: nowrap;
    }
    a, span {
      padding: 0.5em;
      color: inherit;
      cursor: pointer;
      display: block;
    }
    span {
      transition: color $transition-q;
    }
    &:hover {
      color: $color-accent;
    }
    &__arrow {
      width: 6px;
      height: 6px;
      margin-left: 6px;
      fill: $color-accent;
      margin-top: -0.2em;
    }
  }
  .submenu {
    display: flex;
    flex-direction: column;
    list-style: none;
    width: 210px;
    padding: 0;
    margin: 0;
    background: $color-white;
    box-shadow: 0px 4px 40px rgba(34, 74, 164, 0.15);
    z-index: 10;
    color: $color-black;
    a {
      padding: (10em/14) 1em;
      transition: background-color $transition-q, color $transition-q;
      &:hover {
        background: $color-black;
        color: $color-white;
      }
    }
  }
  &__item--has-submenu {
    position: relative;
    .submenu {
      position: absolute;
      bottom: 0;
      right: 0;
      transform: translate(0, 120%);
      opacity: 0;
      visibility: hidden;
      transition: opacity $transition, transform $transition, visibility $transition;
      transition-delay: 0.15s;
    }
    &:hover, &:active, &:focus {
      .submenu {
        opacity: 1;
        transform: translate(0, 100%);
        transition: opacity $transition, transform $transition, pointer-events $transition;
        transition-delay: 0s;
        visibility: inherit;
        &.js-pointer-events {
          // pointer-events: inherit;
        }
      }
    }
  }

  &__mobile-button {
    width: 45px;
    height: 45px;
    background: $color-black;
    padding: 0 10px;
    flex: 0 0 auto;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      display: none;
    }
    &__wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      transition: transform $transition;
      & > div {
        width: 100%;
        height: 3px;
        margin-bottom: 4px;
        background: $color-white;
        border-radius: 0;
        transition: transform $transition, border-radius $transition;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &.active &__wrapper{
      transform: rotate(180deg);
      & > div {
        &:nth-child(1) {
          border-radius: 3px;
          transform: translate(0, 7px) rotate(-45deg);
        }
        &:nth-child(2) {
          transform: scaleX(0);
        }
        &:nth-child(3) {
          border-radius: 3px;
          transform: translate(0, -7px) rotate(45deg);
        }
      }
    }
  }

  &__mobile-button.active + &__list {
    transform: translate(0, 0) rotate(0deg);
  }
}


.header--white {
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    .header__item {
      color: $color-white;
    } 
  }
}
.header--white-arrow {
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    .header__item {
      &__arrow {
        fill: $color-black;
      }
    }
  }
}
.header--white-accent {
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    .header__item {
      &:hover {
        color: $color-accent;
      }
    }
  }
}

.header--white-black {
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    .header__item {
      &:hover {
        color: $color-black;
      }
    }
  }
}
