.grid-worker {
  display: flex;
  margin: -20px;
  flex-wrap: wrap;
  &__item {
    width: 100%;
    padding: 20px;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      width: 50%;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      width: 33.333%;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: 25%;
    }
  }
  &__img {
    max-width: 80px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 10px;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      margin-bottom: 20px;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      margin-bottom: 30px;
      max-width: 98px;
    }
  }
  &__name {
    margin-bottom: 10px;
  }
}