.standard-nav {
  display: flex;
  margin-bottom: $base-size*6;
  flex-wrap: wrap;
  &__btn {
    padding: 4px 0;
    position: relative;
    text-align: center;
    margin-right: $base-size*8;
    margin-bottom: $base-size*2;
    &::after {
      content: '';
      width: 100%;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 0;
      background: $color-accent;
      transition: transform $transition-q;
      transform: scaleX(0);
      transform-origin: 100% 100%;
    }
    &:last-child {
      margin-right: 0;
    }
    &.active {
      &::after {
        transform: scaleX(1);
        transform-origin: 0% 0%;
      }
    }
  }
}

.nav-path {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &__item {
    display: flex;
    align-items: center;
    font-weight: 500;
    margin: 4px 0 6px;
    svg {
      margin: 2px 10px 0;
    }
  }
}