.ibg{
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}
.ibg > img{
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.ibg--1_1 {
  &::before {
    content: '';
    padding-top: 100%;
    display: block;
    width: 0;
  }
}
.ibg--16_9 {
  &::before {
    content: '';
    padding-top: 56.25%;
    display: block;
    width: 0;
  }
}
.ibg--16_10 {
  &::before {
    content: '';
    padding-top: 63%;
    display: block;
    width: 0;
  }
}