.row-contact {
  margin-bottom: -30px;
  & > div {
    margin-bottom: 30px;
  }
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    & > div {
      margin-right: 30px;
      &:last-child {
        margin-right: 0;
      }
    } 
  }
  .col-phone {
    max-width: 210px;
  }
}