.grid-toggle {
  position: relative;
  &__wrapper {
    transition: $transition;
  }

  &__title {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: $base-size 0;
    margin-bottom: $base-size*3;
    transition: color $transition;
    &::after {
      content: '';
      width: 16px;
      height: 16px;
      border-radius: 50%;
      display: inline-block;
      margin-left: auto;
      background: url('../img/arrow.svg') no-repeat center center;
      background-size: 10px;
      background-color: $color-grey;
      transform: rotate(180deg);
      transition: transform $transition, background-color $transition;
    }
    &:hover {
      color: $color-accent;
    }
    &.active {
      &::after {
        background-color: $color-accent;
        transform: rotate(90deg);
        @media (min-width: map-get($grid-breakpoints, 'lg')) {
          transform: rotate(0deg);
        }
      }
    }
    
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: $base-size*52;
    }
  }

  &__content {
    display: none;
    margin-top: -$base-size*1;
    padding-bottom: $base-size*6;
    transition: transform $transition, margin $transition;
    transform-origin: 50% 100%;

    @media (min-width: map-get($grid-breakpoints, 'lg'))  {
      display: block;
      transform: scaleY(0);
      margin-top: 0;
      width: calc(100% - #{$base-size*52});
      max-width: $base-size*144 + 25;
      position: absolute;
      top: 0;
      right: 0;
      padding-left: 25px;
    }
    &:last-child {
      padding-bottom: 0;
    }
    &__title {
      margin-bottom: $base-size*4;
    }
    p:last-child {
      margin-bottom: 0;
    }

    &.active {
      transition: transform $transition, margin 0s;
      @media (min-width: map-get($grid-breakpoints, 'lg')) {
        transform: scaleY(1);
        transform-origin: 50% 0%;
      }
    }
    
    &.hiden-this {
      transform-origin: 50% 0%;
    }
  }
}