.carousel-worker {
  &__nav {
    margin-bottom: 50px;
    .owl-stage-outer {
      overflow: visible;
    }
  }
  .owl-item:last-child &__card {
    margin-right: 0;
  }
  &__card {
    width: 110px;
    text-align: center;
    margin-right: 30px;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: 130px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      margin-right: 40px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      margin-right: 50px;
    }
  }
  &__card.active &__img {
    box-shadow: 0 0 0 10px rgba($color-accent, 0.5);
  }
  &__card.active {
    color: $color-accent;
  }
  &__img {
    width: 98px;
    display: inline-block;
    border-radius: 50%;
    background-color: $color-grey;
    margin: 0 10px 20px;
    max-width: 100%;
    transition: box-shadow $transition;
  }
  &__name {
    transition: color $transition;
  }
  &__info {

  }
  &__table {
    max-width: 890px;
    td:first-child {
      padding-top: (20em/14);
    }
    td:last-child {
      padding-top: 4px;
    }
    tr:first-child td:first-child {
      padding-top: 0;
    }
    td:first-child {
      color: $color-accent;
      font-weight: 700;
      vertical-align: top;
    }
    ul {
      margin-bottom: 0;
    }
    td {
      display: block;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      td:first-child, td:last-child {
        display: table-cell;
        padding-top: (20em/14);
      }
      tr:first-child td {
        padding-top: 0;
      }
      td:first-child {
        padding-right: 1em;
        width: 10em;
      }
    }
  }
}