.grid-services {
  display: flex;
  justify-content: center;
  &__wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: -$base-size*1.6;
    max-width: $base-size*64;
    @media (min-width: 29.68em) {
      max-width: $base-size*140;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      margin: -$base-size*3;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      max-width: none;
      margin: -$base-size*1.6;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    flex: 0 0 calc(100% - #{$base-size*3.2});
    margin: $base-size*1.6;
    color: inherit;
    @media (min-width: 29.68em) {
      flex: 0 0 calc(50% - #{$base-size*3.2});
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      flex: 0 0 calc(50% - #{$base-size*6});
      margin: $base-size*3;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      flex: 0 0 calc(25% - #{$base-size*3.2});
      margin: $base-size*1.6;
    }
  }
  &__content {
    position: relative;
    padding: $base-size*4 $base-size*2 0;
    overflow: hidden;
    transition: background-color $transition ease-in-out, padding-bottom $transition ease-in-out;
    margin-top: 0;
    flex: 1 0 auto;
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      padding: $base-size*4 $base-size*4 0;
    }
  }
  &__title {
    margin-bottom: $base-size*4;
  }
  &__text {
    position: absolute;
    bottom: $base-size*4;
    left: $base-size*2;
    width: calc(100% - #{$base-size*2*2});
    transform: translate(0, 150%);
    transition: transform $transition ease-in-out;
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      left: $base-size*4;
      width: calc(100% - #{$base-size*4*2});
    }
  }
  &__img {
    position: relative;
    flex: 0 0 auto;
    &__icon {
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: opacity $transition ease-in-out;
      z-index: 1;
    }
    &::before {
      transition: background-color $transition ease-in-out, padding-top $transition ease-in-out;
      min-height: $base-size*18;
    }
    &::after {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: $color-black;
      opacity: 0;
      transition: opacity $transition ease-in-out;
    }
  }

  &__item:hover {
    color: $color-white;
  }
  &__item:hover &__content {
    background: $color-accent;
    padding-bottom: calc(4.6em + #{$base-size*4});
  }
  &__item:hover &__text {
    transform: translate(0, 0);
  }
  &__item:hover &__img {
    &__icon {
      opacity: 1;
    }
    &::before {
      padding-top: calc(100% - 4.6em - #{$base-size*4});
    }
    &::after {
      opacity: 0.6;
    }
  }

  @include mediaForPhone {
    &__item {
      color: $color-white;
    }
    &__content {
      background: $color-accent;
      padding-bottom: $base-size*4;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &__item:hover &__content {
      padding-bottom: $base-size*4;
    }
    &__text {
      transform: translate(0, 0);
      position: static;
      width: 100%;
    }
    &__img {
      &__icon {
        opacity: 1;
      }
      &::before {
        padding-top: calc(100% - 4.6em - #{$base-size*4});
      }
      &::after {
        opacity: 0.6;
      }
    }
  }
}