$base-size: 5px;

// Color
$color-black: #32495E;
$color-grey: #8093AC;
$color-white: #F9F9F9;
$color-accent: #E95258;
$color-back-line-section: #49647D;

$color-input: #BDBDBD;

//Font size
$font-size-text-standart: (14rem/16);
$font-size-big-text: 1rem; 
$font-size-btn: $font-size-text-standart;
$font-size-h1: (
  min: (20rem/16), 
  sm: (24rem/16),
  md: (28rem/16),  
  lg: (32rem/16),  
  xl: (38rem/16),  
  xxl: (44rem/16),
);
$font-size-h2: (
  min: (16rem/16),
  sm: (18rem/16),
  lg: (22rem/16),
  xl: (25rem/16),
  xxl: (28rem/16),

  // min: (20rem/16),
  // sm: (22rem/16),
  // lg: (24rem/16),
  // xl: (26rem/16),
  // xxl: (28rem/16),
);
$font-size-h3: (
  min: (14rem/16),
  sm: (16rem/16),
  md: (18rem/16),
  xl: (22rem/16),
  xxl: (24rem/16),
);

//Font weight
$font-regular: 400;
$font-medium: 500;
$font-substrong: 600;
$font-strong: 800;
$font-weight-h1: $font-strong;
$font-weight-h2: $font-strong;
$font-weight-h3: 600;

//Animation
$transition: 0.4s;
$transition-q: 0.2s;

@mixin size-h1 {
  font-size: map-get($font-size-h1, 'min');
  @media (min-width: map-get($grid-breakpoints, 'sm')) {
    font-size: map-get($font-size-h1, 'sm');
  }
  @media (min-width: map-get($grid-breakpoints, 'md')) {
    font-size: map-get($font-size-h1, 'md');
  }
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    font-size: map-get($font-size-h1, 'lg');
  }
  @media (min-width: map-get($grid-breakpoints, 'xl')) {
    font-size: map-get($font-size-h1, 'xl');
  }
  @media (min-width: map-get($grid-breakpoints, 'xxl')) {
    font-size: map-get($font-size-h1, 'xxl');
  }
}

@mixin size-h2 {
  font-size: map-get($font-size-h2, 'min');
  @media (min-width: map-get($grid-breakpoints, 'sm')) {
    font-size: map-get($font-size-h2, 'sm');
  }
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    font-size: map-get($font-size-h2, 'lg');
  }
  @media (min-width: map-get($grid-breakpoints, 'xl')) {
    font-size: map-get($font-size-h2, 'xl');
  }
  @media (min-width: map-get($grid-breakpoints, 'xxl')) {
    font-size: map-get($font-size-h2, 'xxl');
  }
}

@mixin size-h3 {
  font-size: map-get($font-size-h3, 'min');
  @media (min-width: map-get($grid-breakpoints, 'sm')) {
    font-size: map-get($font-size-h3, 'sm');
  }
  @media (min-width: map-get($grid-breakpoints, 'md')) {
    font-size: map-get($font-size-h3, 'md');
  }
  @media (min-width: map-get($grid-breakpoints, 'xl')) {
    font-size: map-get($font-size-h3, 'xl');
  }
  @media (min-width: map-get($grid-breakpoints, 'xxl')) {
    font-size: map-get($font-size-h3, 'xxl');
  }
}

@mixin size-standart {
  font-size: (12rem/16);
  @media (min-width: map-get($grid-breakpoints, 'xl')) {
    font-size: $font-size-text-standart;
  }
}

@mixin size-big-text {
  font-size: (14rem/16);
  @media (min-width: map-get($grid-breakpoints, 'xl'))  {
    font-size: $font-size-big-text;
  }
}

//Font size
@mixin mediaForPhone {
  @media (max-width: 1000px) {
    @content;
  }
  @media (orientation: portrait) {
    @content;
  }
}