.btn {
  color: $color-black;
  font-size: $font-size-btn;
  padding: (8em/14) (25em/14);
  border-radius: (25em/14);
  min-width: (190em/14);
  text-decoration: none;
  flex-shrink: 1;
  background-color: transparent;
  position: relative;
  overflow: hidden;
  transition: color $transition ease-in-out, box-shadow $transition ease-in-out;
  box-shadow: inset 0 0 0 1px $color-black;
  &:hover, &:focus, &:active {
    text-decoration: none;
    color: $color-white;
    box-shadow: inset 0 0 0 1px $color-accent, inset 0 0 2em 3em $color-accent;
  }
}
button.btn {
  min-height: (50em/14);
}
a.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  &::before {
    content: '';
    width: 0;
    display: block;
    height: (34em/14);
  }
}
.btn-hover-black {
  &:hover, &:focus, &:active {
    color: $color-white;
    box-shadow: inset 0 0 3em 3em $color-black;
    border-color: $color-black; 
  }
}
.btn--white {
  color: $color-white;
  border-color: $color-white;
  box-shadow: inset 0 0 0 1px $color-white;
}
.btn--white-hover-black {
  &:hover, &:focus, &:active {
    color: $color-white;
    box-shadow: inset 0 0 0 1px $color-black, inset 0 0 3em 3em $color-black;
    border-color: $color-black; 
  }
}
.btn--accent {
  color: $color-accent;
  border-color: $color-accent;
}
.btn--center {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
}


// Другие кнопки
.btn--to-up {
  background: $color-accent;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  position: relative;
  margin-bottom: (75em/14);
  transition: box-shadow $transition_q;
  svg {
    vertical-align: top;
    animation: btnToUp 1s ease-in-out 0s infinite alternate;
  }
  path {
    fill: $color-white;
  }
  &::after {
    content: 'На вверх';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-54%, 100%) rotate(-90deg);
    white-space: nowrap;
    line-height: 1;
    margin-bottom: (-30em/14);
    pointer-events: none;
  }

  &:hover, &:focus {
    box-shadow: 0px 0px 10px rgba($color-accent, 0.7);
  }
}

.btn--next-scroll {
  width: 60px;
  height: 60px;
  background: #F2C94C;
  border-radius: 50%;
  // display: flex;
  // flex-direction: column;
  // align-items: center;
  // justify-content: center;
  transition: box-shadow $transition_q;
  z-index: 10;
  img {
    display: block;
    margin-right: auto;
    margin-left: auto;
  }
  .mouse {
    margin-bottom: 5px;
    animation: btnToUp 1s ease-in-out 0s infinite alternate;
  }
  &:hover {
    box-shadow: 0px 0px 10px rgba(#F2C94C, 0.7);
  }
}

@keyframes btnToUp {
  0% {
    transform: translate(0, 1px);
  }
  100% {
    transform: translate(0, -1px);
  }
}

.btn-more, .btn-what, .btn-write, .btn-phone, .btn-inst {
  width: 50px;
  height: 50px;
  border: 1px solid #fff;
  border-radius: 50%;
  transition: box-shadow $transition ease-in-out;
}

a {
  &.btn-more, &.btn-what, &.btn-write, &.btn-phone, &.btn-inst {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
}

.btn-more {
  background: $color-accent;
  // animation: btn-more 1.5s infinite;
  box-shadow: 0 0 0 10px rgba($color-accent, 0.5);
  &:hover, &.active {
    // animation-name: none;
    box-shadow: 0 0 0 20px rgba($color-accent, 0.5);
  }
}
.btn-what {
  background: #14AC51;
  box-shadow: 0 0 0 0 rgba(#14AC51, 0.1);
  &:hover {
    box-shadow: 0 0 0 5px rgba(#14AC51, 0.3);
  }
}
.btn-write {
  background: #4598FF;
  box-shadow: 0 0 0 0 rgba(#4598FF, 0.1);
  &:hover {
    box-shadow: 0 0 0 5px rgba(#4598FF, 0.3);
  }
}
.btn-phone {
  background: #32495E;
  box-shadow: 0 0 0 0 rgba(#32495E, 0.1);
  &:hover {
    box-shadow: 0 0 0 5px rgba(#32495E, 0.3);
  }
}
.btn-inst {
  background: #C33A7C;
  box-shadow: 0 0 0 0 rgba(#C33A7C, 0.1);
  &:hover {
    box-shadow: 0 0 0 5px rgba(#C33A7C, 0.3);
  }
}

@keyframes btn-more {
  0% {
    box-shadow: 0 0 0 0 rgba($color-accent, 0.5);
    // transform: scale(1);
  }
  15% {
    // transform: scale(1.05);
  }
  25% {
    // transform: scale(1);
  }
  100% {
    box-shadow: 0 0 0 40px rgba($color-accent, 0);
  }
}