
/* SOCIAL-LIST */
.social-list {
  padding: 0;
  margin: -2px;
  display: flex;
  list-style: none;
  a {
    width: 20px;
    height: 20px;
    display: block;
    margin: 2px;
    background: no-repeat center center;
    background-size: contain;
    transition: opacity $transition-q;
    &:hover {
      opacity: 0.8;
    }
  }
  &__fb {
    background-image: url('../img/icon-fb.svg') !important;
  }
  &__google {
    background-image: url('../img/icon-google.svg') !important;
  }
  &__vk {
    background-image: url('../img/icon-vk.svg') !important;
  }
  &__youtube {
    background-image: url('../img/icon-youtube.svg') !important;
  }
}