.grid-news {
  max-width: 300px;
  margin: auto;
  @media (min-width: map-get($grid-breakpoints, 'sm')) {
    max-width: 600px;
  }
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    max-width: none;
  }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: (-$base-size*1.6);
  }
  &__item {
    width: calc(100% - #{$base-size*3.2});
    background: $color-accent;
    color: $color-white;
    padding: $base-size*4;
    margin: ($base-size*1.6);
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      width: calc(50% - #{$base-size*3.2});
    }
    @media (min-width: map-get($grid-breakpoints, 'lg'))  {
      width: calc(25% - #{$base-size*3.2});
    }
    &:hover {
      color: $color-white;
    }
  }
  &__img {
    margin: #{-$base-size*4} #{-$base-size*4} #{$base-size*4};
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $color-black;
      opacity: 0.2;
      transition: opacity $transition-q;
      z-index: 1;
    }
  }
  &__item:hover &__img::after {
    opacity: 0;
  }
  &__title {
    margin-bottom: $base-size*4;
  }
  &__text {
    margin-bottom: $base-size*4;
  }
  &__link {
    color: inherit;
    display: inline-block;
    &:hover {
      text-decoration: underline;
    }
  }
}