.footer {
  padding-top: 8*$base-size;
  padding-bottom: 8*$base-size;
  background: #333333;
  color: $color-white;
  &__wrapper {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      flex-wrap: nowrap;
    }
  }
  &__col {
    margin-top: 30px;
    width: 50%;
    order: 1;
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-top: 0;
      order: 0;
      width: 180px;
      margin-right: 7*$base-size;
    }
    &__list {
      padding: 0;
      margin: 0;
      list-style: none;
      & > li + li {
        margin-top: (10em/14);
      }
    }
    &__title {
      font-weight: $font-weight-bold;
      margin-bottom: (20em/16);
    }
  }
  &__btn__wrapper {
    margin-left: auto;
    margin-bottom: 0;
    width: 35%;
    text-align: right;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      width: 50%;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg'))  {
      margin-bottom: 5.35714em;
      width: auto;
    }
  }
  &__btn {
    &::after {
      display: none;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      &::after {
        display: block;
      }
    }
  }
  &__logo  {
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      width: 50%;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      width: auto;
      margin-right: 14*$base-size;
    }
  }
  &__copy {
    margin-top: 40px;
  }
}