.content {
  .clearfix {
    clear: both;
  }
  img {
    display: block;
    margin: 0 auto;
    max-height: 80vh;
    margin-bottom: 1rem;
  }
  .right-float {
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      float: right;
      clear: both;
      max-width: 50%;
      margin: 0;
      margin-left: 15px;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      margin-left: 25px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-left: 30px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      margin-left: 45px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      margin-left: 50px;
    }
  }
  .left-float {
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      float: left;
      clear: both;
      margin: 0;
      margin-right: 15px;
      max-width: 50%;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      margin-right: 25px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      margin-right: 30px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xl')) {
      margin-right: 45px;
    }
    @media (min-width: map-get($grid-breakpoints, 'xxl')) {
      margin-right: 50px;
    }
  }
}