/* LOGO */
.logo {
  color: $color-black;
  display: flex;
  align-items: center;
  font-size: (20rem/16);
  @media (min-width: map-get($grid-breakpoints, 'md')) {
    font-size: (24rem/16);
  }
  @media (min-width: map-get($grid-breakpoints, 'xl')) {
    font-size: (28rem/16);
  }
  @media (min-width: map-get($grid-breakpoints, 'xxl')) {
    font-size: (30rem/16);
  }
  &__svg {
    width: (70em/30);
    height: (45em/30);
    flex: 0 0 (70em/30);
    path:nth-child(1) {
      fill: $color-black;
    }
    path:nth-child(2) {
      fill: $color-accent;
    }
  }
  &__text {
    flex: 0 0 auto;
    text-transform: uppercase;
    line-height: 1;
    font-weight: 700;
    margin-left: (20em/30);
  }
}

.logo--not-res-logo {
  .logo__svg {
    width: 70px;
    height: 45px;
  }
}

.logo--white-black {
  color: $color-white;
  .logo__svg {
    path:nth-child(1) {
      fill: $color-black;
    }
    path:nth-child(2) {
      fill: $color-white;
    }
  }
}

.logo--white-black-lg {
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    color: $color-white;
    .logo__svg {
      path:nth-child(1) {
        fill: $color-black;
      }
      path:nth-child(2) {
        fill: $color-white;
      }
    }
  }
} 

.logo--white-accent {
  color: $color-white;
  .logo__svg {
    path:nth-child(1) {
      fill: $color-accent;
    }
    path:nth-child(2) {
      fill: $color-white;
    }
  }
}


.logo--white-accent-lg {
  @media (min-width: map-get($grid-breakpoints, 'lg')) {
    color: $color-white;
    .logo__svg {
      path:nth-child(1) {
        fill: $color-accent;
      }
      path:nth-child(2) {
        fill: $color-white;
      }
    }
  }
}