.modal {
  &-body {
    background: rgba($color-black, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }
  &-wrapper {
    padding: 35px 15px;
    display: flex;
    margin: auto;
    max-width: 100%;
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      padding: 35px 20px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      padding: 35px 20px;
    }
  }
  &-content {
    max-width: 100%;
    position: relative;
  }
  &-exit {
    position: absolute;
    top: 8px;
    right: 8px;
    padding: 10px 10px 5px 5px;
    svg {
      width: 20px;
      transition: opacity $transition-q;
    }
    &:hover {
      svg {
        opacity: 0.7;
      }
    }
    path {
      fill: $color-grey;
    }
  }
}
.modal-body:not(.modal-body--open) {
  visibility: hidden;
}
.modal-body--default {
  .modal-content {
    border-radius: 10px;
    background: $color-white;
    color: $color-black;
    padding: 50px 15px;
    width: 475px;
    @media (min-width: map-get($grid-breakpoints, 'sm')) {
      padding: 50px 20px;
    }
    @media (min-width: map-get($grid-breakpoints, 'md')) {
      padding: 50px 30px;
    }
    @media (min-width: map-get($grid-breakpoints, 'lg')) {
      border-radius: 20px;
      padding: 50px 40px;
    }
  }
}
.modal-body--black {
  .modal-content {
    background: $color-black;
    color: $color-white;
  }
}
.modal-body--media {
  .modal-wrapper {
    max-width: 100%;
    max-height: 100%;
    text-align: center;
  }
  img {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
  }
}
.modal-body--fade-back {
  background: rgba($color-black, 0);
  transition: background-color $transition, visibility $transition 0s;

  &.modal-body--open {
    background: rgba($color-black, 0.8);
  }
}
.modal-body--slide {
  .modal-content {
    opacity: 0;
    transform: scale(1) translateY(80vh);
    transition: transform $transition cubic-bezier(0.165, 0.840, 0.440, 1.000), opacity $transition cubic-bezier(0.165, 0.840, 0.440, 1.000);
  }
  &.modal-body--open {
    .modal-content {
      opacity: 1;
      transform: scale(1) translateY(0vh);
    }
  }
}
